import strings from "./service_selector_strings";
import login_config from "./service_selector_config";
import utils from "helpers/utils";
import spi from "spi";
import Spinner from "components/common/spinner/spinner";
import cx from'classnames';

module.exports = React.createClass({

  displayName: "ServiceSelectorServerLogin",

  getInitialState: function () {
    return {
      error: false,
      valid_url: false,
      loading: false,
      url: ''
    };
  },

  componentWillMount: function () {
    if (this.props.defaultServerUrl && !this.state.url) {
      this.setState({
        url: this.props.defaultServerUrl
      });
    }
  },

  componentDidMount: function () {
    this._focusAndSelect();
    if (this.state.url) {
      this._checkURLValidity(this.state.url);
    }
  },

  _focusAndSelect: function () {
    var input = ReactDOM.findDOMNode(this.refs.url_input);
    if (input !== document.activeElement) {
      input.focus();
      input.select();
    }
  },

  _navigateBack: function () {
    this.props.onUpdateView(login_config.choose_login_key);
  },

  _fullURL: function (url){
    if (url && url !== '') {
      return /^https?:\/\//.test(url) ? url : 'https://' + url;
    }
    return null;
  },

  _cleanUrl: function (url) {
    var cleanUrl;
    if (typeof url === "string") {
      cleanUrl = url.trim();
      if (cleanUrl.slice(-1) === "/") {
        // Remove Trailing slash on url
        cleanUrl = cleanUrl.substring(0, cleanUrl.length - 1);
      }
    }
    return cleanUrl;
  },

  _testUrl: function (url) {
    var xhr = utils.request.getXHR(),
        featuresUrl = strings.get_features_url(url);

    xhr.open("GET", featuresUrl, true);
    xhr.onreadystatechange = () => {
      if (xhr.readyState === 4) {
        if (xhr.status === 200) {
          this._validateServerResponse(JSON.parse(xhr.responseText), url);
        } else {
          this._submitFailure(strings.form_errors.server_not_found);
        }
      }
    };
    xhr.send();
  },

  _submitFailure: function (error) {
    this._checkURLValidity(this.state.url);
    this._setError(error);
    this._stopLoading();
  },

  _validateServerResponse: function (data, url) {
    if (/devvm\.hipchat\.com/.test(url) || (data && 'features' in data && data.features.btf)) {
      // check if we are connecting to a valid server (ie > 1.2.8)
      // feature flag mapping to server version number:

      var server_capabilities = utils.features.btf_capabilities(data.features);

      if (server_capabilities.nonce === true) {
        // make sure the wrapper knows we are logging into server and what the current
        // url is in case we want to save it
        spi.onServerLoginShow(url);
        if (server_capabilities.oauth === true) {
          // do oauth flow for 1.3.4
          this._doServerOauthLogin(url);
        } else {
          this._doServerNonceLogin(url);
        }
      } else {
        this._submitFailure(strings.form_errors.invalid_version);
      }

    } else {
      this._submitFailure(strings.form_errors.invalid_url);
    }
  },

  _validateURL: function(address) {
    var url = this._cleanUrl(address),
        full_url = this._fullURL(url),
        valid = false;

    if (!url) {
      this._setError(strings.form_errors.empty_url);
      this._setInvalidUrl();
      valid = false;
    } else if (!full_url) {
      this._setError(strings.form_errors.invalid_url);
      this._setInvalidUrl();
      valid = false;
    } else {
      this._setValidUrl();
      this._clearError();
      valid = full_url;
    }
    return valid;
  },

  _onChange: function (e) {
    this.setState({
      url: e.target.value
    });
    this._checkURLValidity(e.target.value);
  },

  _onKeyDown: function (e) {
    if (e.keycode === utils.keyCode.Enter) {
      this._onSubmit(e);
    }
  },

  _checkURLValidity: function (val) {
    this._validateURL(val);
  },

  _onSubmit: function (e) {
    e.preventDefault();

    if (this.state.loading) {
      return;
    }

    let val = this.state.url,
        url = this._validateURL(val);

    if (url) {
      this._startLoading();
      this._clearError();
      this._testUrl(url);
    }
  },

  _doServerNonceLogin: function (url) {
    if (url) {
      var location = utils.getWindowLocation();
      this._setValidUrl();
      location.href = strings.sign_in_url(url);
    }
  },

  _doServerOauthLogin: function (url) {
    if (url) {
      // this is where you set the url to the server's /users/authorize url
      spi.onServerOauthLogin(url);
    }
  },

  _startLoading: function () {
    this.setState({
      loading: true
    });
  },

  _stopLoading: function () {
    this.setState({
      loading: false
    });
  },

  _setError: function (msg) {
    this.setState({
      error: msg
    });
  },

  _clearError: function () {
    this.setState({
      error: false
    });
  },

  _setValidUrl: function () {
    this.setState({
      valid_url: true
    });
  },

  _setInvalidUrl: function () {
    this.setState({
      valid_url: false
    });
  },

  _onDownloadClicked: function (e) {
    e.preventDefault();
    spi.onLegacyDownloadLinkClicked();
  },

  _getDownloadLink: function() {
    if (this.state.error === strings.form_errors.invalid_version) {
      return <a href="" id="download_link" onClick={this._onDownloadClicked}>{strings.download_legacy_client_linktext}</a>;
    }
  },

  render: function() {
    let cancelClasses = cx({
          "cancel-button": true,
          "hidden": this.state.loading
        }),
        submitDisabled = (!this.state.valid_url || this.state.loading),
        hrefLink = this._getDownloadLink();

    return (
      <div id="server-login" className="aui-group">
        <div className="aui-item hc-service-server-login">
          <div className="hc-service-server-img"></div>
          <h2>{strings.log_in_to_server}</h2>
          <p>{strings.server_url_input_desc}</p>
          <form ref="server_form" className="aui" onSubmit={this._onSubmit}>
            <input id="url_input" className="text long-field" onChange={this._onChange} onKeyDown={this._onKeyDown} type="text" ref="url_input" placeholder={strings.server_address_placeholder} value={this.state.url} disabled={this.state.loading} />
            <input type="submit" id="submit_btn" className="aui-button aui-button-primary" value={strings.server_login_cta} ref="submitButton" aria-disabled={submitDisabled} />
            <div className="cancel-wrap">
              <Spinner ref="spinner" spin={this.state.loading} size="small"/>
              <a ref="server_back" id="server_back" href="#" onClick={this._navigateBack} className={cancelClasses}>{strings.server_login_cancel}</a>
            </div>
            <div className="error-message-container">
              <p className="error-message">{this.state.error}</p>
              {hrefLink}
            </div>
          </form>
        </div>
      </div>
    );
  }

});



