import ChooseService from "./service_selector_choose_service";
import ServerLogin from "./service_selector_server_login";
import login_config from "./service_selector_config";
import spi from "spi";

module.exports = React.createClass({

  displayName: "ServiceSelectorLayout",

  getInitialState: function () {
    return {
      activeView: login_config.choose_login_key
    };
  },

  _onUpdateView: function (viewName) {
    this.setState({
      activeView: viewName
    });
  },

  _getView: function () {
    if (this.state.activeView === login_config.server_login_key) {
      return <ServerLogin defaultServerUrl={spi.getLastServerUrl()} onUpdateView={this._onUpdateView} />;
    } else if (this.state.activeView === login_config.choose_login_key){
      return <ChooseService onUpdateView={this._onUpdateView} />;
    }
  },

  render: function () {
    var view = this._getView();

    return (
      <div id="hc-service-selector-layout">
        {view}
      </div>
    );
  }

});




