import strings from "./service_selector_strings";
import login_config from "./service_selector_config";
import spi from "spi";

module.exports = React.createClass({

  displayName: "ServiceSelectorChooseService",

  propTypes: {
    onUpdateView: React.PropTypes.func.isRequired
  },

  getDefaultProps: function () {
    return {
      onUpdateView: function () {}
    };
  },

  _showCloudLogin: function() {
    // call the spi so that the client can control what url to go to
    spi.onCloudLoginShow();
  },

  _showServerLogin: function(e) {
    e.preventDefault();
    if (this.props.onUpdateView) {
      this.props.onUpdateView(login_config.server_login_key);
    }
  },

  render: function() {
    return (
      <div id="choose-service" className="aui-group selector">
        <div className="aui-item hc-service-selector-dark">
          <div className="hc-service-cloud-img"></div>
          <h2>{strings.log_in_to_atlassian}</h2>
          <p>{strings.log_in_to_atlassian_desc}</p>
          <a ref="cloud_login" id="cloud_login" onClick={this._showCloudLogin} className="aui-button aui-button-primary">{strings.choose_login_cta}</a>
        </div>
        <div className="aui-item">
          <div className="hc-service-server-img"></div>
          <h2>{strings.log_in_to_server}</h2>
          <p>{strings.log_in_to_server_desc}</p>
          <a ref="server_login" id="server_login" onClick={this._showServerLogin} className="aui-button">{strings.choose_login_cta}</a>
        </div>
      </div>
    );
  }

});


